<template>
  <div>
    <NavTemplent title="部门负责人指标库"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-form-item label="部门">
            <el-select v-model="form.costType" placeholder="请选择部门">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="周期">
            <el-select v-model="form.site" placeholder="请选择站点">
              <el-option v-for="item in siteList" :key="item.website_id" :value="item.website_id" :label="item.website_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getUserList">
              <i class="el-icon-search"></i>
              筛选</el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-row style="border: 1px solid #e6e3e3">
        <el-col :span="12" style="padding: 0 15px">
          <p style="font-size: 14px;font-weight: bolder;padding: 0 15px">
            <span style="text-align: left">人事部门</span>
            <span style="text-align: right">查看详情</span>
          </p>
          <el-row>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder">月数据</p>
              <el-progress type="circle" stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder">约面数</p>
              <el-progress type="circle" stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder">转化率</p>
              <el-progress type="circle" stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder">录入量</p>
              <el-progress type="circle" stroke-width="10" :percentage="25"></el-progress>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="12" style="padding: 0 15px">
          <p style="font-size: 14px;font-weight: bolder;padding: 0 15px">
            <span style="text-align: left">数据对比</span>
            <span style="text-align: right">查看报告</span>
          </p>
          <el-row>
            <el-col :span="18" style="padding: 5px;text-align: center">
              <div id="myChart" style="width: 100%;height: 200px"></div>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
      <el-row style="border: 1px solid #e6e3e3">
        <el-col :span="12" style="padding: 0 15px">
          <p style="font-size: 14px;font-weight: bolder;padding: 0 15px">
            <span style="text-align: left">人事部门</span>
            <span style="text-align: right">查看详情</span>
          </p>
          <el-row>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder">月数据</p>
              <el-progress type="circle" stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder">约面数</p>
              <el-progress type="circle" stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder">转化率</p>
              <el-progress type="circle" stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder">录入量</p>
              <el-progress type="circle" stroke-width="10" :percentage="25"></el-progress>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="12" style="padding: 0 15px">
          <p style="font-size: 14px;font-weight: bolder;padding: 0 15px">
            <span style="text-align: left">数据对比</span>
            <span style="text-align: right">查看报告</span>
          </p>
          <el-row>
            <el-col :span="18" style="padding: 5px;text-align: center">
              <div id="myChart1" style="width: 100%;height: 200px"></div>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                siteList: null,
                editUserMessage: null,
                departmentList: [],
                activeName: 'personal',
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                editUse: null,
                dialogVisible: false,
                promotionList: [],
                roleList: [],
                total: 0,
                updateStatusData: null,
                menu: false,
                title: '编辑费用',
                form: {
                    costType: '',
                    date: [],
                    departmentId: '',
                    password: '',
                    mobile: '',
                    userName: '',
                    department_id: '',
                    email: '',
                    name: '',
                    role_id: [],
                    status: 0
                }
            }
        },
        methods: {
            // 站点列表
            getSiteList() {
                this.$post('/getGaWebSitList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    website_name: this.form.siteName
                }).then(res => {
                    this.siteList = res.list
                })
            },
            drawLine() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                // 绘制图表
                myChart.setOption({
                    tooltip: {
                        trigger: 'item',
                        formatter: "{a} <br/>{b}: {c} ({d}%)"
                    },
                    legend: {
                        bottom: 10,
                        orient: 'vertical',
                        x: 'right',
                        data:['搜索引擎','社交平台','直接输入','其他']
                    },
                    series: [
                        {
                            name:'访问来源',
                            type:'pie',
                            radius: ['50%', '70%'],
                            avoidLabelOverlap: false,
                            label: {
                                normal: {
                                    show: false,
                                    position: 'center'
                                },
                                emphasis: {
                                    show: true,
                                    textStyle: {
                                        fontSize: '30',
                                        fontWeight: 'bold'
                                    }
                                }
                            },
                            labelLine: {
                                normal: {
                                    show: false
                                }
                            },
                            data:[
                                {value:1548, name:'搜索引擎'},
                                {value:335, name:'社交平台'},
                                {value:310, name:'直接输入'},
                                {value:234, name:'其他'}
                            ]
                        }
                    ]
                });
            },
            drawLine1() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart1'));
                // 绘制图表
                myChart.setOption({
                    tooltip: {
                        trigger: 'item',
                        formatter: "{a} <br/>{b}: {c} ({d}%)"
                    },
                    legend: {
                        bottom: 10,
                        orient: 'vertical',
                        x: 'right',
                        data:['搜索引擎','社交平台','直接输入','其他']
                    },
                    series: [
                        {
                            name:'访问来源',
                            type:'pie',
                            radius: ['50%', '70%'],
                            avoidLabelOverlap: false,
                            label: {
                                normal: {
                                    show: false,
                                    position: 'center'
                                },
                                emphasis: {
                                    show: true,
                                    textStyle: {
                                        fontSize: '30',
                                        fontWeight: 'bold'
                                    }
                                }
                            },
                            labelLine: {
                                normal: {
                                    show: false
                                }
                            },
                            data:[
                                {value:1548, name:'搜索引擎'},
                                {value:335, name:'社交平台'},
                                {value:310, name:'直接输入'},
                                {value:234, name:'其他'}
                            ]
                        }
                    ]
                });
            },
            getUserList() {
                this.$post('/getUserList', {
                    user_name: this.form.userName,
                    department_id: this.form.departmentId
                }).then(res => {
                    this.total = res.count;
                    this.userList = res.list
                })
            },
            // 部门列表
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            },
            editUseMessage(data) {
                this.editUserMessage = data;
                this.dialogVisible = true;
                if (data.id) {
                    this.form = data;
                    this.menu = true;
                }else {
                    this.form.mobile = '';
                    this.form.email = '';
                    this.form.name = '';
                    this.form.department_id = '';
                    this.form.role_id = [];
                    this.menu = false;
                }
            },
            updateStatus(data) {
                this.editUserMessage = data;
                if (data.status === 1) {
                    this.dialogVisible = true;
                } else {
                    this.$post('editUserStatus', {
                        user_id: data.id
                    }).then(res => {
                        this.getUserList();
                        this.$message.success('状态修改成功')
                    })
                }
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if (this.editUserMessage.id) {
                    if (this.form.mobile === this.editUserMessage.mobile) {
                        this.mobile = null
                    } else {
                        this.mobile = this.form.mobile
                    }
                    if (this.uditUser) {
                        this.$post('editUser', {
                            email: this.editUserMessage.email,
                            user_id: this.editUserMessage.id,
                            user_name: this.editUserMessage.name,
                            mobile: null,
                            department_id: this.form.department_id,
                            password: this.editUserMessage.password,
                            role_list: JSON.stringify(this.form.role_id)
                        }).then(res => {
                            this.$post('editUserStatus', {
                                user_id: this.editUserMessage.id
                            }).then(res => {
                                this.$message.success('状态修改成功');
                                this.dialogVisible = false;
                                this.$emit('close');
                                this.getUserList()
                            });
                        })
                    } else {
                        this.$post('editUser', {
                            email: this.form.email,
                            user_id: this.editUserMessage.id,
                            user_name: this.form.name,
                            mobile: this.mobile,
                            department_id: this.form.department_id,
                            password: this.form.password,
                        }).then(res => {
                            this.$message.success('用户修改成功');
                            this.dialogVisible = false;
                            this.$emit('close');
                            this.getUserList()
                        })
                    }

                }else {
                    console.log(this.form);
                    this.$post('signUp', this.form).then(res => {
                        this.$message.success('用户新增成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getUserList()
                    })
                }
            }
        },
        mounted() {
            this.getSiteList();
            this.getUserList();
            this.getDepartmentList();
            this.drawLine();
            this.drawLine1()
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
  .tab {
    margin: 10px;
    .active{
      background: #409EFF;
      color: #fff;
    }
    span {
      font-size: 14px;
      border-radius: 5px;
      border: 1px solid #e6e3e3;
      padding: 5px 10px;
    }
  }
</style>



