<template>
  <div>
    <NavTemplent title="总费用"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-form-item label="费用类型">
            <el-select v-model="form.costType" placeholder="请选择费用类型">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="站点">
            <el-select v-model="form.site" placeholder="请选择站点">
              <el-option v-for="item in siteList" :key="item.website_id" :value="item.website_id" :label="item.website_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
          <el-button type="primary">
            <i class="el-icon-search"></i>
            筛选</el-button>
        </el-form-item>
        </el-form>
      </div>
      <el-row style="border: 1px solid #e6e3e3">
        <h5 style="font-weight: bolder;padding: 0 15px">推广费用</h5>
        <el-col :span="12" style="padding: 0 15px;border-right: 1px solid #ccc">
          <h5 style="text-align: center;font-weight: bolder">站点费用统计：lastchic</h5>
          <div id="myChart3" style="width: 100%;height: 300px"></div>
        </el-col>
        <el-col :span="12" style="padding: 0 15px">
          <h5 style="text-align: center;font-weight: bolder">
            ROI排行榜 本月
          </h5>
          <el-tabs v-model="activeName">
            <el-tab-pane label="个人" name="personal">
              <el-table
                :data="promotionList"
                border
                style="width: 100%;height: 250px">
                <el-table-column
                  type="index">
                </el-table-column>
                <el-table-column
                  label="排名">
                  <template slot-scope="scope">
                    <span>{{scope.row.name || '--'}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="姓名">
                  <template slot-scope="scope">
                    <span>{{scope.row.department_name || '--'}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="销售额">
                  <template slot-scope="scope">
                    <span>{{scope.row.role_name || '--'}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="ROI">
                  <template slot-scope="scope">
                    <span>{{scope.row.status}}</span>
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
            <el-tab-pane label="部门" name="department">
              <el-table
                :data="promotionList"
                border
                style="width: 100%;height: 250px">
                <el-table-column
                  type="index">
                </el-table-column>
                <el-table-column
                  label="排名">
                  <template slot-scope="scope">
                    <span>{{scope.row.name || '--'}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="姓名">
                  <template slot-scope="scope">
                    <span>{{scope.row.department_name || '--'}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="销售额">
                  <template slot-scope="scope">
                    <span>{{scope.row.role_name || '--'}}</span>
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
          </el-tabs>
        </el-col>
      </el-row>
      <el-row class="tableTitle">
        <el-col :span="24">
          <h5>物流费用</h5>
        </el-col>
      </el-row>
      <div>
        <div id="myChart" style="width: 100%;height: 300px"></div>
      </div>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                siteList: null,
                departmentList: [],
                activeName: 'personal',
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                promotionList: [],
                total: 0,
                updateStatusData: null,
                menu: false,
                title: '编辑费用',
                form: {
                    costType: '',
                    date: [],
                    site: ''
                }
            }
        },
        methods: {
            // 站点列表
            getSiteList() {
                this.$post('/getGaWebSitList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    website_name: this.form.siteName
                }).then(res => {
                    this.siteList = res.list
                })
            },
            drawLine() {
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                myChart.setOption({
                    tooltip : {
                        trigger: 'axis',
                            axisPointer: {
                            type: 'cross',
                                label: {
                                backgroundColor: '#6a7985'
                            }
                        }
                    },
                    legend: {
                        data:['邮件营销','联盟广告','视频广告','直接访问','搜索引擎']
                    },
                    toolbox: {
                        feature: {
                            saveAsImage: {}
                        }
                    },
                    grid: {
                        left: '3%',
                            right: '4%',
                            bottom: '3%',
                            containLabel: true
                    },
                    xAxis : [
                        {
                            type : 'category',
                            boundaryGap : false,
                            data : ['周一','周二','周三','周四','周五','周六','周日']
                        }
                    ],
                        yAxis : [
                    {
                        type : 'value'
                    }
                ],
                    series : [
                    {
                        name:'邮件营销',
                        type:'line',
                        stack: '总量',
                        areaStyle: {},
                        data:[120, 132, 101, 134, 90, 230, 210]
                    },
                    {
                        name:'联盟广告',
                        type:'line',
                        stack: '总量',
                        areaStyle: {},
                        data:[220, 182, 191, 234, 290, 330, 310]
                    },
                    {
                        name:'视频广告',
                        type:'line',
                        stack: '总量',
                        areaStyle: {},
                        data:[150, 232, 201, 154, 190, 330, 410]
                    },
                    {
                        name:'直接访问',
                        type:'line',
                        stack: '总量',
                        areaStyle: {normal: {}},
                        data:[320, 332, 301, 334, 390, 330, 320]
                    },
                    {
                        name:'搜索引擎',
                        type:'line',
                        stack: '总量',
                        label: {
                            normal: {
                                show: true,
                                position: 'top'
                            }
                        },
                        areaStyle: {normal: {}},
                        data:[820, 932, 901, 934, 1290, 1330, 1320]
                    }
                ]
                });
            },
            drawLine3() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart3'));
                // 绘制图表
                myChart.setOption({
                    tooltip: {
                        trigger: 'item',
                        formatter: "{a} <br/>{b}: {c} ({d}%)"
                    },
                    legend: {
                        bottom: 10,
                        orient: 'vertical',
                        x: 'right',
                        data:['搜索引擎','社交平台','直接输入','其他']
                    },
                    series: [
                        {
                            name:'访问来源',
                            type:'pie',
                            radius: ['50%', '70%'],
                            avoidLabelOverlap: false,
                            label: {
                                normal: {
                                    show: false,
                                    position: 'center'
                                },
                                emphasis: {
                                    show: true,
                                    textStyle: {
                                        fontSize: '30',
                                        fontWeight: 'bold'
                                    }
                                }
                            },
                            labelLine: {
                                normal: {
                                    show: false
                                }
                            },
                            data:[
                                {value:1548, name:'搜索引擎'},
                                {value:335, name:'社交平台'},
                                {value:310, name:'直接输入'},
                                {value:234, name:'其他'}
                            ]
                        }
                    ]
                });
            },
            // 部门列表
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            },
        },
        mounted() {
            this.getSiteList();
            this.getDepartmentList();
            this.drawLine3();
            this.drawLine()
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
  .tab {
    margin: 10px;
    .active{
      background: #409EFF;
      color: #fff;
    }
    span {
      font-size: 14px;
      border-radius: 5px;
      border: 1px solid #e6e3e3;
      padding: 5px 10px;
    }
  }
</style>



