export default axios

/**
 * fetch 请求方法
 * @param url
 * @param params
 * @returns {Promise}
 */
export function get(url, params = {}) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
        params: params
      })
      .then(response => {
        resolve(response.data)
      })
      .catch(err => {
        reject(err)
      })
  })
}

/**
 * post 请求方法
 * @param url
 * @param data
 * @returns {Promise}
 */
export function post(url, data = {}) {
  return new Promise((resolve, reject) => {
    axios.post(url, data).then(
      response => {
        // console.log(response.data.code)
        if (response && response.data) {
          resolve(response.data)
        } else {
          resolve(response)
        }
      },
      err => {
        reject(err)
      }
    )
  })
}

/** 文件下载 */
export function fileDownload(data, fileName) {
  let blob = new Blob([data], {
    type: "application/octet-stream"
  });
  let filename = fileName || "filename.csv";
  if (typeof window.navigator.msSaveBlob !== "undefined") {
    window.navigator.msSaveBlob(blob, filename);
  } else {
    var blobURL = window.URL.createObjectURL(blob); //创建下载的链接
    var tempLink = document.createElement("a"); //创建a标签
    tempLink.style.display = "none";
    tempLink.href = blobURL;
    tempLink.setAttribute("download", filename); //下载后文件名
    if (typeof tempLink.download === "undefined") {
      tempLink.setAttribute("target", "_blank");
    }
    document.body.appendChild(tempLink);
    tempLink.click(); //点击下载
    document.body.removeChild(tempLink); //下载完成移除a元素
    window.URL.revokeObjectURL(blobURL); //释放掉blob对象
  }
}

/** 文件获取 */
export function getFile(url,params = {}) {
  return new Promise((resolve, reject) => {
    try {
      if (!url || typeof url !== 'string') {
        reject();
        throw new Error('url is not empty and needs')
      }
      axios({
        params: {
          ...params
        },
        method: "post",
        url: url,
        responseType: "arraybuffer" // 重要
      })
        .then((response) => {
          resolve(response)
        })
        .catch((error) => {
          reject(error);
          this.$message.error('网络请求出错');
        })
    } catch (error) {
      console.log(error)
    }
  })
}




