<template>
  <div>
    <NavTemplent title="物料费用"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-form-item>
            <el-select v-model="form.status" placeholder="请选择状态">
              <el-option label="待审核" :value="0"></el-option>
              <el-option label="已审核" :value="1"></el-option>
              <el-option label="未通过" :value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getMaterialList">
              <i class="el-icon-search"></i>
              筛选</el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-row>
        <el-col :span="24">
          <div id="myChart" style="width: 100%;height: 400px"></div>
        </el-col>
      </el-row>
      <el-row class="tableTitle">
        <el-col :span="12">
          <h5>物料列表</h5>
        </el-col>
        <el-col :span="12" style="text-align: right">
          <el-button size="mini" @click="downloadData"><i class="el-icon-download"></i>导出</el-button>
          <el-button size="mini" type="primary" @click="editMaterial"><i class="el-icon-plus"></i>添加</el-button>
        </el-col>
      </el-row>
      <el-table
        :data="materialList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="快递袋">
          <template slot-scope="scope">
            <span>￥{{scope.row.package_price}} ({{scope.row.package_num || '--'}}个)</span>
          </template>
        </el-table-column>
        <el-table-column
          label="包装拉链袋">
          <template slot-scope="scope">
            <span>￥{{scope.row.zipper_package_price}} ({{scope.row.zipper_package_num || '--'}}个)</span>
          </template>
        </el-table-column>
        <el-table-column
          label="热敏打印机">
          <template slot-scope="scope">
            <span>￥{{scope.row.ticket_price}} ({{scope.row.ticket_num || '--'}}个)</span>
          </template>
        </el-table-column>
        <el-table-column
          label="总费用">
          <template slot-scope="scope">
            <span>￥{{scope.row.total_price}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="状态">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 0">待审核</span>
            <span v-else-if="scope.row.status === 1">已审核</span>
            <span v-else>未通过</span>
          </template>
        </el-table-column>
        <el-table-column
          label="购买时间">
          <template slot-scope="scope">
            <span>{{ scope.row.buy_time * 1000 | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="editMaterial(scope.row)">编辑</el-button>
            <el-button type="danger" size="mini" @click="delMaterial(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <TablePage :pageSize="page.pageSize" :pageNum="page.pageNum" :total="total"></TablePage>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="快递袋：" label-width="100px">
            <el-input style="width: 40%" v-model="form.package_num" placeholder="请输入个数"></el-input>
            <el-input style="width: 40%" v-model="form.package_price" placeholder="请输入单价"></el-input>
          </el-form-item>
          <el-form-item label="包装拉链袋：" label-width="100px">
            <el-input style="width: 40%" v-model="form.zipper_package_num" placeholder="请输入个数"></el-input>
            <el-input style="width: 40%" v-model="form.zipper_package_price" placeholder="请输入单价"></el-input>
          </el-form-item>
          <el-form-item label="热敏打印机：" label-width="100px">
            <el-input style="width: 40%" v-model="form.ticket_num" placeholder="请输入个数"></el-input>
            <el-input style="width: 40%" v-model="form.ticket_price" placeholder="请输入单价"></el-input>
          </el-form-item>
          <el-form-item label="购买日期：" label-width="100px">
            <el-date-picker
              v-model="form.buy_time"
              type="date"
              placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="上传图片：" label-width="100px">
<!--            <el-input type="file" v-model="imageUrl"></el-input>-->
            <el-upload action='' :on-change="getFile" :limit="1" list-type="picture" :auto-upload="false">
              <el-button size="small" type="primary">选择图片上传,最多上传一张图片</el-button>
            </el-upload>
          </el-form-item>
          <el-form-item label="审核" v-if="auditCost">
            <el-radio-group v-model="form.audit" @change="editAudit">
              <el-radio label="通过"></el-radio>
              <el-radio label="未通过"></el-radio>
            </el-radio-group>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
          <el-button size="mini" type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import moment from 'moment'
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                imageUrl: '',
                auditCost: false,
                title: '编辑物料费用',
                date: '',
                materialList: null,
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                editUse: null,
                dialogVisible: false,
                total: 0,
                download: '',
                editMaterialMessage: null,
                totalCost: [],
                dateList: [],
                form: {
                    audit: '',
                    status: '',
                    date: [moment(moment().valueOf() - 7 * 24 * 3600 * 1000).format('YYYY/MM/DD'), moment(moment() + 24 * 3600 * 1000).format('YYYY/MM/DD')],
                    image: '',
                    buy_time: '',
                    ticket_num: '',
                    ticket_price: '',
                    zipper_package_price: '',
                    zipper_package_num: '',
                    package_price: '',
                    package_num: ''
                }
            }
        },
        methods: {
            getFile(file, fileList) {
                this.imageUrl = file.raw;
                console.log(file.raw);
                this.getBase64(file.raw).then(res => {
                    console.log(res)
                })
            },
            getBase64(file) {
                return new Promise(function(resolve, reject) {
                    let reader = new FileReader();
                    let imgResult = "";
                    reader.readAsDataURL(file);
                    reader.onload = function() {
                        imgResult = reader.result;
                    };
                    reader.onerror = function(error) {
                        reject(error);
                    };
                    reader.onloadend = function() {
                        resolve(imgResult);
                    };
                });
            },
            drawLine() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                // 绘制图表
                myChart.setOption({
                    title: {
                        text: '物料费用',
                        left: 'center'
                    },
                    xAxis: {
                        type: 'category',
                        data: this.dateList
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: this.totalCost,
                        type: 'line'
                    }]
                });
            },
            getMaterialList() {
                this.$post('/getMaterialList', {
                    page: this.page.pageSize,
                    pageNum: this.page.pageNum,
                    start_time: this.form.date[0],
                    end_time: this.form.date[1],
                    status: this.form.status,
                }).then(res => {
                    this.total = res.count;
                    this.materialList = res.list;
                    this.dateList = res.list.map(item =>{
                        return moment(item.buy_time * 1000).format('YYYY/MM/DD')
                    });
                    this.totalCost = res.list.map(item => {
                        return item.total_price
                    });
                    this.drawLine()
                })
            },
            downloadData() {
                this.download = true;
                this.$getFile('/getMaterialList', {
                    page: this.page.pageSize,
                    pageNum: this.page.pageNum,
                    start_time: this.form.date[0],
                    end_time: this.form.date[1],
                    status: this.form.status,
                    download: this.download
                }).then(res => {
                    let filename = "material.csv" + moment().format('YYYY/MM/DD');
                    this.$fileDownload(res.data, filename);
                })
            },
            editMaterial(data) {
                if (data.status === 0) {
                    this.auditCost = true;
                } else {
                    this.auditCost = false;
                }
                this.editMaterialMessage = data;
                this.dialogVisible = true;
                if (data.material_cost_id) {
                    data.buy_time = moment(data.buy_time * 1000).format('YYYY/MM/DD');
                    this.form = data;
                }else {
                    this.form.buy_time = '';
                    this.form.ticket_num = '';
                    this.form.ticket_price = '';
                    this.form.zipper_package_price = '';
                    this.form.zipper_package_num = '';
                    this.form.package_price = '';
                    this.form.package_num = '';
                }
            },
            editAudit() {
                this.arr = [];
                this.arr.push(this.editMaterialMessage.material_cost_id);
                if (this.form.audit === '通过') {
                    this.audit = 1;
                    this.text = '确定通过审核'
                } else {
                    this.audit = 2;
                    this.text = '确定不通过审核'
                }

                this.$confirm(this.text + ', 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$post('editMateriaStatus', {
                        status: this.audit,
                        material_cost_id: JSON.stringify(this.arr)
                    }).then(() => {
                        this.$message.success('审核完成')
                    })
                }).catch(() => {
                    this.form.audit = '';
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            delMaterial(data) {
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$post('delMaterial', {
                        material_cost_id: data.material_cost_id
                    }).then(() => {
                        this.getMaterialList();
                        this.$message.success('物料费用删除成功')
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if (this.editMaterialMessage.material_cost_id) {
                    this.$post('editMaterial', {
                        material_cost_id: this.editMaterialMessage.material_cost_id,
                        package_num: this.form.package_num,
                        package_price: this.form.package_price,
                        zipper_package_num: this.form.zipper_package_num,
                        zipper_package_price: this.form.zipper_package_price,
                        ticket_price: this.form.ticket_price,
                        ticket_num: this.form.ticket_num,
                        buy_time: moment(this.form.buy_time).format('YYYY/MM/DD'),
                        image: this.imageUrl
                    }).then(() => {
                        this.$message.success('物料信息修改成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getMaterialList()
                    })
                } else {
                    this.$post('addMaterial', {
                        package_num: this.form.package_num,
                        package_price: this.form.package_price,
                        zipper_package_num: this.form.zipper_package_num,
                        zipper_package_price: this.form.zipper_package_price,
                        ticket_price: this.form.ticket_price,
                        ticket_num: this.form.ticket_num,
                        buy_time: moment(this.form.buy_time).format('YYYY/MM/DD'),
                        image: this.form.image
                    }).then(() => {
                        this.getMaterialList();
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.$message.success('物料信息新增成功')
                    })
                }
            }
        },
        mounted() {
            this.getMaterialList();
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select, .date{
    width: 100%;
  }
  .avatar-uploader .el-upload {
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



